html, body {
  margin: 1px;
  padding: 1px;
  overflow: hidden;
  width: 100vw;
  height: 100vh;
  font-family: 'Segoe UI', Verdana, sans-serif;
  font-size: small;
  display: grid;
  grid-template-columns: auto auto auto;
  grid-template-rows: auto auto 25%;
  grid-template-areas:
    "navbar navbar navbar"
    "sidebar main sidebar-right"
    "sidebar section sidebar-right";
  background-color: #f0f0f0;
}


nav {
  grid-area: navbar;
  background: #f9f9f9;
  display: flex;
  align-items: center;
  padding: 0 10px; 
  border-radius: 5px;
}

nav img {
  height: 50px;
  margin: 5px 0;
  display: block;
}


aside.sidebar {
  grid-area: sidebar;
  background: #f9f9f9;
  padding: 20px;
  border-right: 1px solid #ddd;
  overflow-y: auto;
  width: auto;
  z-index: 1;
  
   
}

aside.sidebar-right {
  float: right;
  grid-area: sidebar-right;
  background: #f9f9f9;
  padding: 20px;
  overflow-y: auto;
  width: auto;
  z-index: 1;
}

main {
  grid-area: main;
  display: flex;
  padding: 0;
  margin: 0;
  border: none;
  border-radius: 0;
  background: #f9f9f9;
  overflow: hidden;
  z-index: 0;
}

#canvas-container {
  flex: 1;
  width: 100%;
  height: 100%;
  position: relative;
  background: #f9f9f9;
  z-index: 0;
}

canvas {
  display: block;
  width: 100%;
  height: 100%;
  z-index: 0;
}



section {
  grid-area: section;
  padding: 10px;
  background: #f9f9f9;
  overflow: hidden;
  overflow-y: scroll;
}

h1 {
  font-weight: 640;
  font-size: large;
  margin-bottom: 8px;
  margin-top: 8px;

}

h2{
  font-weight: 640;
  font-size: medium;
  margin-bottom: 8px;
  margin-top: 8px;
}

h3 {
  font-weight: 380;
  font-size: small;
  margin-bottom: 4px;
  margin-top: 4px;

}


.options-container {
  margin-bottom: 12px;
  background: #f5f5f5;
  padding: 6px;
  border-radius: 8px;
  border: 1px solid #ddd;
}



.options-container img{

  width: 250px;
  height: auto;
  
}

label {
  font-weight: 600;
  display: block;
  margin-bottom: 6px;
}

.slider-group {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-bottom: 10px;
}

input[type="range"], input[type="number"] {
  border: 1px solid #ccc;
  border-radius: 4px;
}

input[type="range"] {
  flex: 1;
}

input[type="number"] {
  width: 70px;
  padding: 4px;
}

.info-icon {
  display: inline-block;
  margin-left: 5px;
  cursor: pointer;
  background: #007bff;
  color: white;
  border-radius: 50%;
  width: 18px;
  height: 18px;
  font-size: 12px;
  text-align: center;
  line-height: 18px;
  position: relative;
}

.tooltip {
  display: none;
  position: absolute;
  top: -5px;
  left: 110%;
  background: #fff;
  color: #333;
  border: 1px solid #ccc;
  padding: 8px;
  border-radius: 5px;
  width: 120px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.2);
  z-index: 9999;
}

.info-icon:hover .tooltip {
  display: block;
}

.tooltip img {

  height: 100px;
  width: auto;

}

