.Mail {
  margin-top: 20px;
  text-align: center;
}

.Mail button {
  font-size: 24px;
  padding: 15px 30px;
  background-color: #079DDF;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  width: 100%;
}

.Mail button:hover {
  transform: scale(1.005);
}

.MailPopup {
    width: 25%;
    height: auto;
    padding: 20px;
    border-radius: 10px;
    background-color: #f9f9f9;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    visibility: hidden;
    border: 1px solid #ddd;   
  }

  .MailPopup .info{

  margin-bottom: 12px;
  background: #f5f5f5;
  padding: 6px;
  border-radius: 8px;
  border: 1px solid #ddd;   

  }

  .MailPopup.open-popup {
    visibility: visible;
  }

  .MailPopup .bevestig {
    
    width: 49%;

    background-color: #0fa0d5;
    color: #ffffff;

  }

    .MailPopup .terug {
    width: 49%;
    background-color: #efb13d;
    color: #ffffff;
   
  }

  .MailPopup{
    text-align: left;
  }

